

























































import { Component, Ref, Vue } from 'vue-property-decorator';
import * as commonTypes from '@/types/common';
import { IInputInfo } from '@/types/interface';
import banner from '../../components/Banner.vue';
import MainFooter from '../../components/MainFooter.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';
import inputCustom from '../../components/InputCustom.vue';

@Component({
  components: {
    banner,
    MainFooter,
    inputFormPanel,
    inputCustom,
  },
})

export default class SetCondition extends Vue {
  @Ref('inputFormBase') readonly inputFormBase!: inputFormPanel;
  @Ref('inputFormAdv') readonly inputFormAdv!: inputFormPanel;

  isFold = true;

  baseInputList = [commonTypes.INPUT_CK_TEST_SUBJECT_COURSE, commonTypes.INPUT_CK_TEST_AMOUNT,
    commonTypes.INPUT_CK_TEST_SCOPE];

  advInputList = [commonTypes.INPUT_CK_TEST_TYPE, commonTypes.INPUT_CK_TEST_LEVEL,
    commonTypes.INPUT_CK_TEST_SUBJECT];

  keyword = '';

  showLogs() {
    this.$router.push('/member/test_record');
  }

  history() {
    this.$router.push('/test/history');
  }

  reset() {
    this.inputFormBase.resetSelection();
    this.inputFormAdv.resetSelection();
    this.keyword = '';
  }

  confirm() {
    const info: IInputInfo | undefined = this.inputFormBase.getInput();
    console.log(info);
    this.$router.push('/test/main');
  }
}
